  font-family: 'Roboto-Bold';
  font-weight: 700;
  font-style: normal;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
}

@font-face {
  font-family: 'Roboto-Light';
  font-weight: 300;
  font-style: normal;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('truetype');
}

@font-face {
  font-family: 'Roboto-Regular';
  font-weight: 400;
  font-style: normal;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

* {
  padding: 0px;
  margin: 0px;
}

:root {
  --backgroundColor: rgb(99, 38, 157);
  --activeDegreeButton: rgb(145, 102, 186);
}

html {
  font-size: 62.5%;
}

body {
  background-color: var(--backgroundColor);
  display: grid;
  justify-items: center;
}

input {
  font-family: 'Roboto-Bold', sans-serif;
  font-size: 2.5rem;
  outline: none;
  padding: 10px;
  border: none;
  background-color: var(--backgroundColor);
  color: white;
  letter-spacing: 1px;
  width: 300px;
}

input::placeholder {
  color: white;
}

.search {
  display: grid;
  justify-items: center;
}

.search-row {
  display: grid;
  grid-auto-flow: column;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid white;
}

.search-action {
  height: 25px;
  width: 25px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
}

.error {
  margin-top: 5px;
  font-family: 'Roboto-Light', sans-serif;
  color: red;
  font-size: 2rem;
  min-height: 30px;
}

.c-f {
  margin-top: 20px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.c,
.f {
  font-family: 'Roboto-Bold', sans-serif;
  font-size: 2rem;
  padding: 10px;
  width: 100px;
  border-radius: 10px;
  background-color: var(--backgroundColor);
  border: 1px solid white;
  color: white;
  cursor: pointer;
}

.c:hover,
.f:hover {
  background-color: var(--activeDegreeButton);
}

.selected {
  background-color: var(--activeDegreeButton);
}

.weather {
  color: white;
  margin-top: 20px;
}

.current {
  display: grid;
  gap: 10px;
}

.condition {
  font-family: 'Roboto-Regular', sans-serif;
  font-size: 2.5rem;
}

.city-country {
  font-family: 'Roboto-Bold', sans-serif;
  font-size: 3.5rem;
}

.date {
  font-family: 'Roboto-Regular', sans-serif;
  font-size: 2.5rem;
}

.temperature {
  font-family: 'Roboto-Bold', sans-serif;
  font-size: 10rem;
}

.currentConditionImage {
  width: 15rem;
  height: 15rem;
}

.main-data {
  display: grid;
  grid-auto-flow: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

.important-data {
  font-family: 'Roboto-Light', sans-serif;
  font-size: 2rem;
}

.important-data img {
  width: 5rem;
  height: 5rem;
}

.important-data > * {
  display: grid;
  grid-auto-flow: column;
  justify-items: start;
  align-items: center;
  gap: 2rem;
}

footer {
  font-family: 'Roboto-Light', sans-serif;
  font-size: 2rem;
  color: white;
  position: absolute;
  bottom: 0;
}

footer a:visited {
  color: black;
}

.loading {
  width: 10rem;
  height: 10rem;
  position: absolute;
  top: 50%;
}
